import React, { useState, useEffect } from 'react';
import './History.css';

export default function History(props) {
    const getLocalData = () => {
        const data = localStorage.getItem("addresses");
        if (data) {
            return JSON.parse(data);
        }
        else {
            return [];
        }
    }

    // ********************************************************************

    const allData = getLocalData();
    const [hight, setHight] = useState({ height: '800px' });

    useEffect(() => {
        if (allData.length > 6) {
            setHight({ height: `${allData.length / 7 * 800}px` });
        }
        else {
            setHight({ height: `800px` });
        }
    }, [allData]);

    return (
        <>
            <div className="history-main-body" style={hight}>
                {!allData.map(item => item.classIcon).includes("fa-solid fa-bookmark") || allData.length === 0 ? (
                    <h3>No favourite address available. Please first bookmark from the history.</h3>
                ) : (
                    <>
                        {[...allData].reverse().map((element) => {
                            if (element.classIcon === "fa-solid fa-bookmark") {
                                return (
                                    <div className="history-box">
                                        <div className="icon-add">
                                            <i className="fa-solid fa-location-dot"></i>
                                            <p>{element.address}</p>
                                        </div>
                                    </div>
                                )
                            }
                            else {
                                return null;
                            }
                        })}
                    </>
                )}

            </div>
        </>
    )
}
